@charset "UTF-8";
:global(*) {
  -webkit-tap-highlight-color: transparent;
  font-family: "微软雅黑";
  user-select: none; }

:global(a),
:global(a):visited {
  text-decoration: none; }

:global(ul) {
  margin: 0;
  padding: 0; }

:global(html),
:global(body),
:global(#root) {
  width: 100%;
  height: 100%;
  margin: 0;
  padding: 0;
  overflow: visible; }

:global(bottom) {
  outline: none; }

:global(body, h1, h2, h3, h4, h5, h6, hr, p, blockquote,
dl, dt, dd, ul, ol, li,
pre,
form, fieldset, legend, button, input, textarea,
th, td) {
  margin: 0;
  padding: 0; }
